<template>
	<v-container :dark="false" :value="true">
		<v-data-table
			title="Select a race to engage with"
			:headers="headers"
			:items="users2render"
			class="elevation-1"
			:loading="loading"
			fixed-header
			dense
			single-select
			@click:row="engage"
			style="cursor: pointer"
		>
			<template slot="top">
				<v-toolbar-title style="padding: 20px; text-align: center">
					Select a race to engage with
				</v-toolbar-title>
			</template>
		</v-data-table>
	</v-container>
</template>

<script>
import db from "@/common/services/firebaseUserService";
import { collection, onSnapshot } from "firebase/firestore";
import { setCurrentRaceUser } from "../../modules/seller/services/firebaseServices";
import { mapActions } from "vuex";

export default {
	data() {
		return {
			loading: true,
			users: [],
			snapshootUnsubscribe: null,
			headers: [
				{ text: "User name", value: "name" },
				{ text: "Sellers engaged", value: "sellers", align: "start" },
				{ text: "Ongoing quiz ", value: "ongoingQuiz", align: "start" },
				{ text: "Total quiz ", value: "race.length", align: "start" },
			],
		};
	},
	created() {
		this.snapshootRef = onSnapshot(collection(db, "users"), (sn) => {
			this.loading = false;
			sn.docChanges().map((change) => {
				let d = { ...change.doc.data(), name: change.doc.id };
				if (change.type === "added") {
					console.log("added", d);
					this.users.push(d);
				}
				if (change.type === "modified") {
					console.log("modified", d);
				}
				if (change.type === "deleted") {
					console.log("deleted", change.doc.data());
				}
			});
		});
	},
	beforeDestroy() {
		if (typeof this.snapshootUnsubscribe === "function")
			this.snapshootUnsubscribe();
	},
	computed: {
		users2render() {
			return this.users
				.filter((u) => u.type === "user" && Array.isArray(u.race) && !u.raceDone)
				.map((u) => {
					let ongoingQuiz = u.race.filter((r) => !r.selected).length;
					return {
						...u,
						sellers: u.sellers && Object.entries(u.sellers).length,
						ongoingQuiz,
					};
				});
		},
	},
	methods: {
		...mapActions("common", ["updateCurrentUser"]),
		async engage(item) {
			// alert(item.name);
			try {
				await setCurrentRaceUser(item.name);
				this.updateCurrentUser({ raceUser: item.name });
				if (typeof this.snapshootUnsubscribe === "function")
					this.snapshootUnsubscribe();
				this.$toast.success(
					`You are engaged to ${item.name}'s race now`
				);
				this.$router.push("/seller");
			} catch (e) {
				this.$toast.error(e.message);
			}
		},
	},
};
</script>

<style></style>
